.callback .container {
  display: flex;
  padding: 0;
}

.callback-content-wrapper {
  background-color: #f4f4f4;
  padding: 78px 30px 82px 30px;
  width: 100%;
}

.callback-title {
  font-weight: 300;
  font-size: 38px;
  line-height: 1.26;
  margin-bottom: 34px;
}

.callback-form {
  display: flex;
  flex-direction: column;
}

.callback-label.name {
  margin-bottom: 36px;
}

.label-name {
  font-size: 12px;
  line-height: calc(14 / 12);
  color: #757575;

  margin-bottom: 4px;
  display: block;
}

.callback-label-input {
  width: 100%;
  background: var(--secondary-text-color);
  border: 1px solid #f5f5f5;
  height: 40px;
  padding: 6px 6px 5px 5px;
}

.callback-label-input::placeholder {
  line-height: 1.56;
  color: #808080;
}

.callback-label-input:focus-within {
  outline: 0;
  border: 1px solid var(--accent-color);
}

.callback-label.email {
  margin-bottom: 60px;
}

.callback-btn {
  background: var(--accent-color);
  border-radius: 5px;
  font-family: 'Open Sans';
  line-height: 1.39;
  text-align: center;
  color: var(--secondary-text-color);
  padding: 7px 0 10px 0;
  border: 0;
}
